.sectionProjects {
  width: 100vw;
  font-family: $roboto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  &__title {
    margin-top: 50px;
    margin-bottom: 50px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 25px;
    color: $grey;
  }
}

.item {
  list-style: none;
  margin-bottom: 20px;
  &__article {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    &--title {
      width: 100px;
      margin-right: 15px;
      margin-left: 15px;
      font-family: $roboto;
      font-size: 15px;
      font-weight: bold;
      color: $orange;
      text-transform: uppercase;
      text-decoration: none;
    }
    &--title:hover {
      color: $easySnorlax;
    }
    &--photo {
      width: 100px;
      height: 100px;
      border: solid $softOrange 2px;
      border-radius: 15%;
      margin-right: 15px;
    }
    &--text {
      width: 150px;
      font-size: 15px;
      margin-right: 15px;
      color: $metal;
    }
  }
}

@media all and (min-width: 768px) {
  .sectionProjects {
    width: 100vw;
    &__title {
      font-size: 30px;
      margin-top: 50px;
      margin-bottom: 20px;
    }
    &__list {
      margin-top: 20px;
    }
  }
  .item {
    &__article {
      &--title {
        width: 100px;
        margin-right: 35px;
        margin-left: 0px;
        font-size: 20px;
      }
      &--photo {
        width: 120px;
        height: 120px;
        border: solid $softOrange 3px;
        border-radius: 15%;
        margin-right: 15px;
      }

      &--text {
        font-size: 18px;
        margin-right: 0px;
        width: 250px;
      }
    }
  }
}

@media all and (min-width: 1200px) {
  .sectionProjects {
    &__title {
      margin-bottom: 20px;
    }
    &__list {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }
  .item {
    &__article {
      width: 400px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      text-align: center;
      &--title {
        width: 320px;
        margin-bottom: 25px;
        margin-left: 20px;
      }
      &--photo {
        width: 250px;
        height: 250px;
        margin-bottom: 25px;
      }
      &--text {
        width: 300px;
        text-align: center;
      }
    }
  }
}
